/* The Modal (background) */

.new-card {
  background-color: rgb(244, 255, 129);

  padding: 1em 0;
  margin: 0.5rem;

  min-height: 250px;
  min-width: 225px;
  -ms-flex-preferred-size: 18%;
      flex-basis: 18%;

  border-radius: 5px;

  display: grid;
  grid-template-columns: 1fr 5fr 1fr;
  -ms-flex-align: center;
      align-items: center;

  -webkit-box-shadow: 4px 8px 0px 0px rgb(229, 241, 113);

          box-shadow: 4px 8px 0px 0px rgb(229, 241, 113);
}

.new-card__content {
  grid-column-start: 2;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;

  text-align: center;
  font-weight: 100;
  overflow: hidden;
}

.new-card__content-text {
  max-height: 115px;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  overflow: hidden;
  display: block;
  height: 50vh;
  width: 100%;
  font-size: 1.5em;
  border: none;
  background: inherit;
  margin: 0;
  text-align: center;
}

.new-card-form__form-select,
.new-card-form__form-textarea,
.new-card-form__form-button {
  font-family: 'Raleway', sans-serif;
  font-size: 1.25em;
}

.new-card-form__form-select {
  background: inherit;
  font-size: 3em;
  border: none;
  width: 55%;
  -ms-flex-item-align: center;
      align-self: center;
}

.new-card-form__form-button {
  background-color: inherit;
  border: 1px solid black;
  font-size: 1em;
  height: 1.5em;
}



